<template lang="html">
  <div class="table__list">
    <table style="width:100%">
      <tr>
        <th>Date</th>
        <th>Student</th>
        <th>Description</th>
        <th>Token Transaction</th>
      </tr>
      <tr v-for="(data, i) in sortedItems">
        <td align="center">{{ data.transaction_date | formatDate }}</td>
        <td align="center">{{ data.student_name }}</td>
        <td align="center">{{ data.description }}</td>
        <td><div :class="data.token_status">{{ data.token_amount }}</div></td>
      </tr>
    </table>
  </div>
</template>

<script>
import { MomentFilter } from '../../components/mixins/MomentFilter.js'

export default {
  mixins: [MomentFilter],
  props: [
    "userTokenList"
  ],
  computed: {
    sortedItems() {
      function compare(a, b) {
        if (a.transaction_date < b.transaction_date)
          return 1;
        if (a.transaction_date > b.transaction_date)
          return -1;
        return 0;
      }

      return this.userTokenList.sort(compare);
    },
  }
}
</script>

<style lang="scss">
</style>



