<template lang="html">
  <div class="token">
    <div class="token--left">
      <TokenFilter :userToken="userToken" />
      <TokenList :userTokenList="userTokenList" />
    </div>
    <!-- <div class="token--right">
      <TokenRequest :userToken="userToken" />
    </div> -->
  </div>
</template>

<script>
import TokenRequest from '../../coach-components/coach-token/TokenRequest'
import TokenFilter from '../../coach-components/coach-token/TokenFilter'
import TokenList from '../../coach-components/coach-token/TokenList'

export default {
  components: {
    TokenRequest,
    TokenFilter,
    TokenList
  },
  data() {
    return {
      myData: null
    }
  },
  mounted () {
    setTimeout(() => {
      this.myData = 'Example Data';
    }, 3000)
    console.log(this.$store.state.DataAdditional)
  },
  methods: {
    getToken (event) {
      const token = this.$store.state.UserToken
      console.log(token);
    }
  },
  computed: {
    userToken () {
      return this.$store.state.UserToken
    },
    userTokenList () {
      return this.$store.state.stateTokenFilterDate.data
    }
  },
  created () {
    this.$store.dispatch("GET_COIN");
    this.$store.dispatch("GET_PROFILE_TOKEN_HISTORY")
  }
}
</script>

<style lang="scss">
  @import './CoachToken.scss';
</style>



