<template lang="html">
  <div class="token__header">
    <div class="token__header--left">
      <div class="view__header__title">
        <div>
          <h2>Token History</h2>
          <div class="blue__circle"></div>
        </div>
      </div>
      <div class="view__header__filter">
        <span>Filter sessions by date range</span>
        <div class="header__filter">
          <label>From</label>
          <div class="datepicker--border">
            <v-menu
              ref="menu"
              v-model="menu"
              :close-on-content-click="false"
              :nudge-right="40"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              min-width="290px"
            >
              <v-text-field
                slot="activator"
                v-model="start_time"
                append-icon="event"
                @click="togleDatepicker"
              ></v-text-field>
              <v-date-picker
                v-model="start_time"
                v-if="menu"
                no-title
              >
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="menu = false">Cancel</v-btn>
                <v-btn flat color="primary" @click="menu = false">OK</v-btn>
              </v-date-picker>
            </v-menu>
          </div>
          <label>To</label>
          <div class="datepicker--border">
            <v-menu
              ref="menu"
              v-model="menu2"
              :close-on-content-click="false"
              :nudge-right="40"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              min-width="290px"
            >
              <v-text-field
                slot="activator"
                v-model="end_time"
                append-icon="event"
                @click="togleDatepicker2"
              ></v-text-field>
              <v-date-picker
                v-model="end_time"
                v-if="menu2"
                no-title
                :min="start_time"
              >
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="menu2 = false">Cancel</v-btn>
                <v-btn flat color="primary" @click="menu2 = false">OK</v-btn>
              </v-date-picker>
            </v-menu>
          </div>
          <v-btn
            class="btn--33"
            depressed
            small
            color="#375197"
            style="color:#fff;"
            @click="filterDate"
          >
          Apply Filter
          </v-btn>
          <v-btn
            class="btn--33"
            depressed
            small
            color="#375197"
            style="color:#fff;"
            @click="resetFilter"
          >
          Reset Filter
          </v-btn>
        </div>
      </div>
    </div>
    <div class="token__header--right">
      <div class="token__balance--coach">
        <h3>Token Balances</h3>
        <h1>{{ userToken.token_amount }}</h1>
      </div>
    </div>
  </div>
</template>

<script>
import { format, subDays, addDays } from 'date-fns';

export default {
  props: [
    'userToken'
  ],
  data () {
    return {
      menu: false,
      menu2: false,
      start_time: null,
      end_time: null,
    }
  },
  mounted () {

  },
  methods: {
    onDateRangeChange(range) {
      this.range = range;
    },
    togleDatepicker () {
      this.menu = !this.menu
    },
    togleDatepicker2 () {
      this.menu2 = !this.menu2
    },
    filterDate () {
      const rangeDate = {
        start_time: this.start_time,
        end_time: this.end_time
      }

      this.$store.dispatch("POST_TOKEN_FILTER_DATE", rangeDate)
    },
    resetFilter () {
      this.$store.dispatch("GET_PROFILE_TOKEN_HISTORY")
      this.start_time = null
      this.end_time = null
    }
  },
}
</script>

<style lang="scss">

</style>



